/* index.css */

/* 전체 페이지 스타일 */
body {
  background-color: white; /* 보라색 배경 */
  font-family: Arial, sans-serif;
  margin: 0; /* body의 margin을 제거합니다. */
  padding: 0; /* body의 padding을 제거합니다. */
}

/* App 컴포넌트 스타일 */
.App {
  text-align: center;
  padding: 20px;
}

/* 영화 목록 스타일 */
.movie-list {
  display: grid;
  grid-template-columns: repeat(8, 1fr); /* 8열로 나열 */
  gap: 20px; /* 각 영화 사이 간격 */
  justify-content: center;
  margin: 20px;
}

/* 각 영화 아이템 스타일 */
.movie {
  position: relative;
  background-color: #3f4486;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  cursor: pointer;
  height: 350px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.movie img {
  width: 100%;
  height: 280px;
  display: block;
  object-fit: fill;
}

/* 영화 정보 스타일 변경 */
.movie-info {
  background-color: #3f4486; /* 짙은 보라색 배경 */
  color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between; /* 영화 제목과 평점 사이에 최대 간격 부여 */
  align-items: center;
  height: 50px; /* 영화 정보 영역의 높이 설정 */
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.movie-title {
  text-align: left; /* 영화 제목 왼쪽 정렬 */
  margin-right: 10px; /* 제목과 평점 사이의 간격 */
  font-size: 12px;
  font-weight: bold;
}

.movie-rating {
  text-align: right; /* 영화 평점 오른쪽 정렬 */
  font-size: 13px;
}

/* 영화 개요 스타일 */
.movie-description {
  text-align: left;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7); /* 반투명 배경 */
  font-size: 11px;
  color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  display: none; /* 초기에는 숨겨짐 */
  z-index: 1; /* 영화 정보 영역 위로 올라오게 설정 */
}

/* 호버 시 영화 개요 표시 */
.movie:hover .movie-description {
  display: block; /* 호버 시 개요를 보여줍니다. */
}

.movie-description-title {
  font-weight: bold; /* bold로 영화 제목 스타일링 */
  font-size: 13px;
  margin-bottom: 10px; /* 영화 제목 아래 여백 추가 */
}

.movie-description p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 15; /* 영화 개요가 15줄을 넘어가면 ... 표시 */
  overflow: hidden;
  margin: 0;
}
